/* Basic 
================================================== */
body {
	background: #f3f3f4;
	font-family: "open sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
a {
	color: #293c4e;
	-webkit-transition: 0.25s;
	transition: 0.25s;
}
a:hover,
a:active,
a:focus {
	color: #2e4c69;
	outline: none;
	text-decoration: none;
}
























































































/* Header
================================================== */
header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 1030;
}
header .navbar {
	background-color: #FFF;
	border-bottom: 1px solid #e6e6e6;
	-webkit-box-shadow: 0 0 6px 0 rgba(0,0,0, 0.1);
	        box-shadow: 0 0 6px 0 rgba(0,0,0, 0.1);
	height: 50px;
	margin-bottom: 0;
	margin-left: 210px;
	-webkit-transition: 0.25s;
	transition: 0.25s;
}
header .navbar .nav a {
	color: #5e5e5e;
}
body > .navbar .nav > li > a:hover,
body > .navbar .nav > li > a:active,
body > .navbar .nav > li > a:focus,
body > .navbar .nav .open > a,
body > .navbar .nav .open > a:hover,
body > .navbar .nav .open > a:focus {
	background: #ffffff;
	color: #999999;
}
body > .navbar .navbar-right > .nav {
	margin-right: 10px;
}
header .navbar .sidebar-toggle {
	float: left;
	padding: 9px 5px;
	margin-top: 8px;
	margin-right: 0;
	margin-bottom: 8px;
	margin-left: 5px;
	background-color: transparent;
	background-image: none;
}
header .navbar .sidebar-toggle:hover .icon-bar {
	background: #999999
}
header .navbar .sidebar-toggle .icon-bar {
	display: block;
	width: 22px;
	height: 2px;
	background: #333333;
}
header .navbar .sidebar-toggle .icon-bar + .icon-bar {
	margin-top: 4px;
}
header .navbar .navbar-header {
	width: 20%;
	float: left;
}
header .navbar .navbar-header .navbar-form .form-control {
	border: 0!important;
	font-size: 13px;
	background: #f3f3f4;
}
header .navbar  .navbar-header .navbar-form button {
	background: #f3f3f4;
	border-radius: 0 5px 5px 0;
}
header .navbar .nav > li.user > a {
	font-weight: bold;
}
header .navbar .nav > li.user > a > i {
	margin-right: 5px;
}
header .navbar .nav > li > a > .label {
	position: absolute;
	top: 7px;
	right: 5px;
	font-size: 10px;
	width: 18px;
	height: 16px;
	line-height: 1.0em;
	text-align: center;
	padding: 2px;
}
header .logo {
	background-color: #172b3e;
	color: #f9f9f9;
	float: left;
	font-size: 20px;
	padding: 0;
	width: 210px;
	font-weight: 600;
	height: 50px;
	display: block;
}
