/* Icons List */
.icon-list {
	margin: 20px -15px 30px -15px;
}
.icon-list div {
	line-height: 26px;
	height: 26px;
	padding-left: 10px;
}
.icon-list div i {
	width: 26px;
	font-size: 14px;
	display: inline-block;
	text-align: right;
	margin-right: 10px;
}
.icon-list div:hover i {
	font-size: 26px;
	vertical-align: -4px;
}
























































































/* Modal */
.modal-header {
	background: #f5f5f5;
	-webkit-box-shadow: inset 0 1px 0 0 #FFF;
	        box-shadow: inset 0 1px 0 0 #FFF;
	border-radius: 5px 5px 0 0;
	color: #696969; 
	text-shadow: 1px 1px 0 #FFF;
}
.modal-header i {
	margin-right: 7px;
}
.modal-footer {
	margin-top: 0;
}
























































































/* Pagination */
.pagination > li > a {
	color: #666;
	-webkit-box-shadow: 0 1px 3px 0 rgba(0,0,0, 0.06);
	        box-shadow: 0 1px 3px 0 rgba(0,0,0, 0.06);
}
.nav.nav-pills {
	margin-bottom: 10px;
}
.nav.nav-pills > li > a {
  border-top: 3px solid transparent;
  border-radius: 0;
  color: #444;
  padding: 13px 10px;
}
.nav.nav-pills > li > a > i {
  margin-right: 5px;
}
.nav.nav-pills > li.active > a,
.nav.nav-pills > li.active > a:hover {
  background-color: #f6f6f6;
  border-top-color: #3c8dbc;
  color: #444;
}
.nav.nav-pills > li.active > a {
  font-weight: 600;
}
.nav.nav-pills.nav-stacked > li > a {
  border-top: 0;
  background-color: #f6f6f6;
  border-left: 3px solid transparent;
  color: #444;
}
.nav.nav-pills > li > a:hover {
  background-color: #FFF;
}
.nav.nav-pills.nav-stacked > li.active > a,
.nav.nav-pills.nav-stacked > li.active > a:hover {
  background-color: #27ae60;
  color: #FFF;
}
























































































/* Mailbox */
.mailbox-text {
	font-size: 13px;
}
.mailbox-avatar {
	width: 100px;
	height: 100px;
	margin: 5px 10px 0 0;
}
.mailbox-info{
	background: #f0efef;
	padding: 10px 10px 10px 0;
	margin-bottom: 10px;
}
.mailbox-info .from  span{
	background: #dcdcdc;
	padding: 10px 12px;
	margin-right: 10px;
}
.mailbox-info .from a {
	color: #5a5a5a;
}
.mailbox-info span.label {
	color: #5a5a5a;
	font-weight: normal;
}
.mailbox-info i {
	margin-right: 5px;
}
.mailbox-attachment {
	background: #f0efef;
	padding: 10px;
	margin-top: 15px;
	line-height: 30px;
}
.mailbox-attachment small {
	margin-left: 10px;
}
.mailbox .mailbox-tools {
	background: rgba(0, 0, 0, 0.03);
	margin-bottom: 10px;
	padding: 10px;
	width: 100%;
}
.mailbox .mailbox-buttons {
	margin-bottom: 10px;
}
.mailbox .box-bordered input, .mailbox .box-bordered textarea {
	margin-bottom: 10px;
}
.mailbox .search-form {
	margin-bottom: 15px;
}
.mailbox .table tr.unread > td {
	background-color: rgba(0, 0, 0, 0.03);
	color: #000;
	font-weight: 600;
}
.mailbox .table tr > td > .fa.fa-star,
.mailbox .table tr > td > .fa.fa-star-o,
.mailbox .table tr > td > .glyphicon.glyphicon-star,
.mailbox .table tr > td > .glyphicon.glyphicon-star-empty {
	color: #f39c12;
	cursor: pointer;
}
.mailbox .table tr > td.small {
	width: 32px;
}
.mailbox .table tr > td.name {
	font-weight: 600;
	width: 145px;
	color: #444;
}
.mailbox .table tr > td.time {
	text-align: right;
	width: 140px;
}
.mailbox .table tr > td.time i {
	margin-right: 10px;
}
.mailbox .table tr > td {
	padding: 13px 10px;
}
.mailbox .table tr > td > a {
	color: #444;
}
























































































/* ------ Pace ------ */
.pace .pace-progress {
  background: #0f99f5;
  position: fixed;
  z-index: 2000;
  top: 0;
  left: 0;
  height: 2px;
  -webkit-transition: width 1s;
  transition: width 1s;
}
.pace-inactive {
  display: none;
}
























































































/* ------ Flot ------ */
.flot {
	height: 350px;
	margin: 15px 0 10px 0;
}
#flot_tip {
	position: absolute;
	display: none;
	padding: 2px 5px;
	color: #FFF;
	background-color: #000;
	opacity: 0.80;
}
.flot-placeholder {
	width: 100%;
	height: 100%;
	font-size: 14px;
	line-height: 1.2em;
}
.legendColorBox {
	padding-left: 10px;
}
.legendLabel {
	padding-left: 4px;
}
























































































/*  Lockscreen */
.login .lock-image {
	text-align: center; 
	margin-bottom: 20px;
}
.login .lock-image .name {
	-webkit-box-shadow: 1px 1px 0 0 rgba(255,255,255, 0.1), inset 1px 1px 3px 0 rgba(0,0,0, 0.3);
	        box-shadow: 1px 1px 0 0 rgba(255,255,255, 0.1), inset 1px 1px 3px 0 rgba(0,0,0, 0.3);
	background: #13212f; 
	margin: 30px -20px 0 0px; 
	padding: 10px 25px 10px 20px; 
	color: #FFF; 
	border-radius: 6px 0 0 6px; 
	display: inline-block;
}
.login .lock-image .img-circle {
	width: 100px;
	-webkit-box-shadow: 1px 1px 0 0 rgba(255,255,255, 0.1), inset 1px 1px 3px 0 rgba(0,0,0, 0.3);
	        box-shadow: 1px 1px 0 0 rgba(255,255,255, 0.1), inset 1px 1px 3px 0 rgba(0,0,0, 0.3);
	background: #13212f; 
	padding: 4px;
	position: relative; 
	z-index:9999
}
.login .lock-image .action {
	-webkit-box-shadow: 1px 1px 0 0 rgba(255,255,255, 0.1), inset 1px 1px 3px 0 rgba(0,0,0, 0.3);
	        box-shadow: 1px 1px 0 0 rgba(255,255,255, 0.1), inset 1px 1px 3px 0 rgba(0,0,0, 0.3);
	background: #13212f; 
	padding: 10px 2px 10px 0px; 
	color: #FFF; 
	margin-left: -7px; 
	border-radius: 0 6px 6px 0; 
	display: inline-block;
}
.login .lock-image .action a {
	background: #e74c3c; 
	color: #FFF;
	padding: 8px 15px; 
	border-radius: 0 6px 6px 0;
}
























































































/* Login, Register etc.. */
.login {
	background-color: #273849;
}
.login .well {
	background: rgba(0,0,0, 0.3);
	border-radius: 6px;
	padding: 10px 20px;
	-webkit-box-shadow: 1px 1px 0 0 rgba(255,255,255, 0.1), inset 1px 1px 3px 0 rgba(0,0,0, 0.3);
	        box-shadow: 1px 1px 0 0 rgba(255,255,255, 0.1), inset 1px 1px 3px 0 rgba(0,0,0, 0.3); 
	border: none;
}
.login .logo {
	font-size: 36px;
	margin: 0 auto!important;
	text-align: center;
	border-radius: 6px;
	padding-bottom: 20px;
}
.login .logo span {
	font-size: 32px;
}
.login .logo a {
	color: #FFF;
}
.login .wrapper {
	width: 430px;
	margin: 140px auto 0 auto;
}
.login .box {
	-webkit-box-shadow: 0 0 6px 0 rgba(0,0,0, 0.2);
	        box-shadow: 0 0 6px 0 rgba(0,0,0, 0.2);
	border-radius: 6px;
	margin-bottom: 0;
}
.login .box small {
	margin-left: 5px;
	color: #828282;
}
.login .box .header {
	background: #2b4a6a;
	padding: 12px 10px 15px 17px;
	font-size: 20px;
	font-weight: 600;
	color: #fff;
	border-radius: 5px 5px 0 0;
	-webkit-box-shadow: inset 0 1px 0 0 rgba(255,255,255, 0.1);
	        box-shadow: inset 0 1px 0 0 rgba(255,255,255, 0.1);
	text-shadow: 1px 1px 0 rgba(0,0,0, 0.8);
}
.login .box .header a { 
	color: rgba(0,0,0, 0.6);
	text-shadow: 1px 1px 0 rgba(255,255,255, 0.3);
}
.login .box .header i {
	margin-right: 10px;
}
.login .box-footer {
	padding: 15px 0 0 0;
}
.login a {
	color: rgba(255,255,255, 0.5);
}
.login a:hover {
	color: rgba(255,255,255, 0.7);
}
.login .box-extra {
	padding: 10px 0;
}
.footer-position {
	bottom: 0;
}
footer {
	color: rgba(255,255,255, 0.4);
	border-top: 1px solid rgba(0,0,0, 0.6);
	-webkit-box-shadow: inset 0 1px 0 0 rgba(255,255,255, 0.1);
	        box-shadow: inset 0 1px 0 0 rgba(255,255,255, 0.1);
	text-shadow: 1px 1px 0 rgba(0,0,0, 0.3);
	padding: 20px 0;
	margin-top: 50px;
	text-align: center;
}
footer ul {
	margin-top: 15px;
}
footer ul li a {
	font-size: 12px;
}
























































































